:root {
  --clr-white: #fff;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: 'OpenSans', sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0;
}

p {
  padding: 0;
  margin: 0;
}

ul,
ol {
  padding: 0;
  margin: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
}

button {
  cursor: pointer;
  background-color: transparent;
  border-style: none;
  outline: none;
}

.site-container {
  overflow: hidden;
}

@font-face {
  font-family: 'Lato';
  src: url("../fonts/Lato-Regular.woff2") format("woff2");
  font-display: swap;
  font-style: normal;
  font-weight: 400;
}

@font-face {
  font-family: 'Lato';
  src: url("../fonts/Lato-Bold.woff2") format("woff2");
  font-display: swap;
  font-style: normal;
  font-weight: 700;
}

body {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Lato';
  font-weight: 400;
  background: linear-gradient(to right, #7b4397, #dc2430);
}

.form {
  text-align: center;
  border: 2px solid var(--clr-white);
  border-radius: 20px;
  padding: 30px;
  color: var(--clr-white);
}

.form__title {
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 20px;
}

.form__input {
  border-radius: 5px;
  padding: 5px 10px;
  width: 70%;
  margin-right: 10px;
}

.form__btn {
  background-color: var(--clr-white);
  border-radius: 3px;
  padding: 6px 15px;
  text-transform: uppercase;
  font-weight: 700;
  font-family: 'Lato';
}

.form .result {
  margin-top: 20px;
  font-size: 24px;
}
